        </div><h2>Row props</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>gutter</td>
<td>栅格间隔</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>flexMode</td>
<td>是否启用flex布局</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>align</td>
<td>flex 布局下的垂直对齐方式，可选值为top、middle、bottom</td>
<td>String</td>
<td>''</td>
</tr>
<tr>
<td>justify</td>
<td>flex 布局下的水平排列方式，可选值为start、end、center、space-around、space-between</td>
<td>String</td>
<td>''</td>
</tr>
</tbody>
</table>
<h2>Col props</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>span</td>
<td>栅格的占位格数，可选值为0~24的整数，为 0 时，相当于display:none</td>
<td>Number | String</td>
<td>0</td>
</tr>
<tr>
<td>order</td>
<td>栅格的顺序</td>
<td>String | Number</td>
<td>''</td>
</tr>
<tr>
<td>offset</td>
<td>栅格左侧的间隔格数，间隔内不可以有栅格</td>
<td>String | Number</td>
<td>''</td>
</tr>
<tr>
<td>push</td>
<td>栅格向右移动格数</td>
<td>String | Number</td>
<td>''</td>
</tr>
<tr>
<td>pull</td>
<td>栅格向左移动格数</td>
<td>String | Number</td>
<td>''</td>
</tr>
</tbody>
</table>

